@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background: linear-gradient(to left, rgb(57, 156, 57), rgb(81, 81, 190));
}

img {
  width: 300px;
}

.movie-container {
  display: flex;
  flex-wrap: wrap;
}

.movie {
  background-color: rgb(140, 190, 190);
  position: relative;
  margin: 1rem;
  width: 300px;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.7);
}

.movie-img {
  max-width: 100%;
}

.movie-info {
  display: flex;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
}

.movie-info h3 {
  margin: 0;
}

.movie-over {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background-color: rgba(255, 255, 255, 0.2);
  padding: 1rem;
  -webkit-transform: translateY(102%);
          transform: translateY(102%);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  overflow: auto;
  max-height: 100%;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.movie:hover .movie-over {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.logo {
  max-width: 30%;
}

.logo:hover {
  cursor: pointer;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

.container {
  margin: 1rem;
  text-align: center;
  position: relative;
  box-shadow: 3px 5px 4px rgba(0, 0, 0, 0.7);
  overflow: hidden;
}

h1 {
  position: absolute;
  top: 0;
  padding: 0.5rem;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 100%;
  background-color: rgba(247, 247, 247, 0.2);
  box-shadow: 2px 5px 4px rgba(0, 0, 0, 0.7);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  font-size: 1.3rem;
}

.container > .vote {
  position: absolute;
  background-color: rgba(240, 248, 255, 0.3);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  border-radius: 50%;
  border: 10px solid #333;
  padding: 0.5rem;
  bottom: 5px;
  left: 5px;
}

.container > .info {
  position: absolute;
  overflow: auto;
  width: 100%;
  padding: 0.5rem;
  border: 0 0 5px 5px;
  -webkit-transform: translateY(102%);
          transform: translateY(102%);
  background-color: rgba(247, 247, 247, 0.5);
  box-shadow: 3px -5px 4px rgba(0, 0, 0, 0.7);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  bottom: 0;
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

img {
  width: 100%;
  height: 100%;
}

.container:hover > .info {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  z-index: 1;
}

.btn {
  display: block;
  position: fixed;
  bottom: 20px;
  right: 30px;
  text-align: center;
  width: 4em;
  z-index: 1;
  height: 4em;
  border: 0.5em solid #333;
  border-radius: 50%;
  box-shadow: 3px 4px 7px rgba(0, 0, 0, 0.7);
  background: rgba(223, 220, 220, 0.3);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
}

.btn::after {
  content: "";
  display: inline-block;
  margin-top: 0.8rem;
  width: 1.4em;
  height: 1.4em;
  border-top: 0.5em solid #333;
  border-right: 0.5em solid #333;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.btn:hover {
  cursor: pointer;
  border: 0.5rem solid #c42a2a;
}

.btn:hover::after {
  border-top: 0.5rem solid #c42a2a;
  border-right: 0.5rem solid #c42a2a;
}

.skeleton {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
  justify-items: center;
  text-align: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.placeholder-item {
  box-shadow: 0 4px 10px 0 rgba(33, 33, 33, 0.15);
  border-radius: 4px;
  width: 300px;
  height: 450px;
  background-color: #d1cbcb;
  position: relative;
  overflow: hidden;
}

.placeholder-item::before {
  content: "";
  display: block;
  position: absolute;
  left: -150px;
  top: 0;
  height: 100%;
  width: 150px;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    #e8e8e8 50%,
    rgba(255, 255, 255, 0) 100%
  );
  -webkit-animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
          animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}

@-webkit-keyframes load {
  from {
    left: -150px;
  }
  to {
    left: 100%;
  }
}

@keyframes load {
  from {
    left: -150px;
  }
  to {
    left: 100%;
  }
}

/* header */
header {
  background-color: rgb(34, 80, 80);
  box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.7);
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  max-width: 30%;
}

.logo:hover {
  cursor: pointer;
}

.search {
  font-family: "Roboto";
  font-size: clamp(0.7rem, 2vw, 1.5rem);
  outline: none;
  border: 2px solid rgb(114, 155, 155);
  border-radius: 50px;
  background-color: transparent;
  padding: 0.5rem 1.5rem;
}

/* btn */
.btn-container {
  margin: 3rem 0;
  display: flex;
  justify-content: space-evenly;
  text-align: center;
}

.btn-container > button {
  width: 100px;
  height: 35px;
  border-radius: 5px;
  border: none;
}

.btn-container:hover > .not-allowed {
  cursor: not-allowed;
}

.btn-container > button:hover {
  cursor: pointer;
  background-color: rgb(212, 206, 206);
}

.btn-container > .relative {
  border-radius: 5px;
  height: 35px;
  width: 50px;
  border: 2px solid black;
  position: relative;
}

.relative > h4 {
  position: absolute;
  font-weight: normal;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

